"use client";

import { Suspense, useEffect, useMemo, useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";

type Payload = {
  fortune: string | null;
  compatibility: string | null;
  dog: { name: string; breed: string; sex: string; neutered: boolean | null; birth: string | null };
  owner: { name: string; earthlyBranch: string };
};

function ResultInner() {
  const params = useSearchParams();
  const router = useRouter();
  const [data, setData] = useState<Payload | null>(null);
  const [error, setError] = useState<string | null>(null);

  const textToShare = useMemo(() => {
    if (!data) return "";
    const d = data;
    return `üê∂ PawStars Í≤∞Í≥º\nÏù¥Î¶Ñ: ${d.dog.name}\nÍ≤¨Ï¢Ö: ${d.dog.breed}\n${d.dog.birth ? `ÏÉùÏùº: ${d.dog.birth}\n` : ""}${d.dog.sex !== "unknown" ? `ÏÑ±Î≥Ñ: ${d.dog.sex === "male" ? "ÏàòÏª∑" : "ÏïîÏª∑"}${d.dog.neutered === true ? " (Ï§ëÏÑ±Ìôî)" : d.dog.neutered === false ? " (ÎØ∏Ï§ëÏÑ±Ìôî)" : ""}\n` : ""}${d.owner?.name ? `Ï£ºÏù∏: ${d.owner.name}\n` : ""}${d.owner?.earthlyBranch ? `Ï£ºÏù∏ 12ÏßÄÏßÄ: ${d.owner.earthlyBranch}\n` : ""}${d.fortune ? `\nÏò§ÎäòÏùò Ïö¥ÏÑ∏: \n${d.fortune}\n` : ""}${d.compatibility ? `\nüêæ Í∂ÅÌï©: \n${d.compatibility}` : ""}`;
  }, [data]);

  useEffect(() => {
    const key = params.get("key");
    if (!key) {
      setError("Í≤∞Í≥º ÌÇ§Í∞Ä ÏóÜÏñ¥Ïöî. Ï≤òÏùåÎ∂ÄÌÑ∞ Îã§Ïãú ÏßÑÌñâÌï¥ Ï£ºÏÑ∏Ïöî.");
      return;
    }
    try {
      const raw = sessionStorage.getItem(`pawstars:${key}`);
      if (!raw) {
        setError("Ï†ÄÏû•Îêú Í≤∞Í≥ºÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏñ¥Ïöî. Îã§Ïãú ÏßÑÌñâÌï¥ Ï£ºÏÑ∏Ïöî.");
        return;
      }
      const parsed = JSON.parse(raw) as Payload;
      setData(parsed);
    } catch {
      setError("Í≤∞Í≥ºÎ•º Î∂àÎü¨Ïò§Îäî Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏñ¥Ïöî.");
    }
  }, [params]);

  const share = async () => {
    if (!data) return;
    const text = textToShare;
    try {
      type KakaoTextShare = {
        objectType: "text";
        text: string;
        link: { mobileWebUrl: string; webUrl: string };
      };
      const w = globalThis as unknown as {
        Kakao?: { isInitialized?: () => boolean; Share: { sendDefault: (opts: KakaoTextShare) => void } };
      };
      if (w?.Kakao && w.Kakao.isInitialized?.()) {
        w.Kakao.Share.sendDefault({ objectType: "text", text, link: { mobileWebUrl: location.href, webUrl: location.href } });
        return;
      }
      if (navigator.share) {
        await navigator.share({ title: "PawStars Í≤∞Í≥º", text });
        return;
      }
      await navigator.clipboard.writeText(text);
      alert("Í≤∞Í≥ºÎ•º ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóê Î≥µÏÇ¨ÌñàÏñ¥Ïöî.");
    } catch {
      try {
        await navigator.clipboard.writeText(textToShare);
        alert("Í≤∞Í≥ºÎ•º ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóê Î≥µÏÇ¨ÌñàÏñ¥Ïöî.");
      } catch {
        alert("Í≥µÏú†Ïóê Ïã§Ìå®ÌñàÏñ¥Ïöî. Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.");
      }
    }
  };

  return (
    <div className="min-h-screen w-full max-w-xl mx-auto px-4 py-5 sm:px-6 sm:py-10">
      <h1 className="text-xl sm:text-2xl font-bold mb-5 sm:mb-6">PawStars Í≤∞Í≥º</h1>
      {error ? (
        <div className="text-red-500 text-sm">{error}</div>
      ) : !data ? (
        <div>Î∂àÎü¨Ïò§Îäî Ï§ë...</div>
      ) : (
        <div className="space-y-6">
          {data.fortune ? (
            <div className="rounded-lg border border-black/10 dark:border-white/20 p-4 whitespace-pre-wrap leading-6">
              {data.fortune}
            </div>
          ) : null}
          {data.compatibility ? (
            <div className="rounded-lg border border-black/10 dark:border-white/20 p-4 whitespace-pre-wrap leading-6">
              {data.compatibility}
            </div>
          ) : null}
          <div className="flex flex-col sm:flex-row gap-3 pt-2">
            <button
              className="w-full sm:w-auto rounded-md bg-black text-white dark:bg-white dark:text-black px-4 py-3"
              onClick={() => router.push("/")}
            >
              Îã§Ïãú Î∂ÑÏÑùÌïòÍ∏∞
            </button>
            <button
              className="w-full sm:w-auto rounded-md border border-black/10 dark:border-white/20 px-4 py-3"
              onClick={share}
            >
              Í≤∞Í≥º Í≥µÏú†ÌïòÍ∏∞
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default function ResultPage() {
  return (
    <Suspense fallback={<div className="min-h-screen w-full max-w-xl mx-auto px-4 py-5 sm:px-6 sm:py-10">Î∂àÎü¨Ïò§Îäî Ï§ë...</div>}>
      <ResultInner />
    </Suspense>
  );
}


